import { AfterViewInit, Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { PsKeyinInputComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-field-keyin/ps-keyin-input/ps-keyin-input.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsActionIcon, IOptionsPsContainerItem, IOptionsPsKeyinInput } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsInputCountExposed } from './ps-input-count.component.interfaces';


@Component({
  selector: 'ps-input-count',
  templateUrl: './ps-input-count.component.html',
})
export class PsInputCountComponent extends PsKeyinInputComponent implements OnInit, OnChanges, AfterViewInit {
  @Input() options: IOptionsPsInputCountExposed;
  public defaultOptions: IOptionsPsKeyinInput = {}

  decrementIconOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-dropdown-circle',
    iconPosition: 'end'
  };
  incrementIconOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-dropup-circle',
    iconPosition: 'end'
  };
  currentInputValue: number;
  maxValue: number;
  minValue: number;
  defaultValue;
  hideDefaultOptions: boolean;
  containerItemOptions: IOptionsPsContainerItem = {
    // hideImageAndIconIfNotPresent: true
  };
  filedCust: any;
  unlimitedValue;
  constructor(commonP: PsCommonService, loggerP: LoggerService) {
    super(commonP, loggerP);
  }

  ngOnInit() {
    this.unlimitedValue = this.commonProv.translate('unlimited_key');  //Mohanad AZDB-6448 - 19112024
    this.filedCust = this.commonProv.getElementValidations(this.options.fcName);
    this.commonProv.copyObject(this.defaultOptions, this.options, false);
    if (PsCommonSettings.custMode) {
      this.defaultOptions.type = 'number';
    }
  }

  ngAfterViewInit() {
    this.checkMinMax();
    if (this.defaultValue || (this.defaultOptions.group.controls[this.defaultOptions.fcName]?.value && this.defaultOptions.group.controls[this.defaultOptions.fcName]?.value !== this.unlimitedValue)) {
      this.unSetDefaultValues()
    } else {
      this.setDefaultValues();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.options.previousValue !== undefined) {
      this.defaultOptions = changes.options.currentValue;
      this.checkMinMax();
    }
  }

  doCrements(type) {
    let countValue = type === 'decrement' ? this.currentInputValue - 1 : this.currentInputValue + 1;
    if (!this.hideDefaultOptions) {
      if (this.defaultValue !== undefined && CommonUtils.parseInt(JSON.parse(this.defaultValue).value) !== 0) {
        this.currentInputValue = CommonUtils.parseInt(JSON.parse(this.defaultValue).value);
        countValue = type === 'decrement' ? this.currentInputValue - 1 : this.currentInputValue + 1;
      }
      this.unSetDefaultValues();
    }
    if (typeof (countValue) === 'number' && countValue >= this.minValue && countValue <= this.maxValue) {
      this.currentInputValue = countValue;
      if (this.currentInputValue === this.minValue) {
        this.decrementIconOptions.isDisabled = true;
      }
      if (this.currentInputValue === this.maxValue) {
        this.incrementIconOptions.isDisabled = true;
      }
      this.defaultOptions.group.controls[this.options.fcName]?.setValue(this.currentInputValue);

    } else if (typeof (countValue) === 'number') {
      if (countValue >= this.maxValue) {
        this.currentInputValue = this.maxValue;
        this.decrementIconOptions.isDisabled = false;
        this.incrementIconOptions.isDisabled = true;
      } else {
        this.currentInputValue = this.minValue;
        this.decrementIconOptions.isDisabled = true;
        this.incrementIconOptions.isDisabled = false;
      }
      this.defaultOptions.group.controls[this.options.fcName]?.setValue(this.currentInputValue);

    }
  }

  onEmptyCounter(value) {
    if (value === '') {
      this.setDefaultValues();
    }
  }

  checkMinMax() {
    this.defaultValue = this.filedCust?.DEFAULT_VALUE;
    this.minValue = this.filedCust?.MIN_VALUE || 0;
    this.maxValue = this.filedCust?.MAX_VALUE || Infinity;
  }

  handleValuesDisplay() {
    this.currentInputValue = +this.defaultOptions.group.controls[this.defaultOptions.fcName]?.value || (this.defaultValue ? CommonUtils.parseInt(JSON.parse(this.defaultValue).value) : this.minValue);
    this.defaultOptions.group.controls[this.defaultOptions.fcName]?.setValue(this.hideDefaultOptions ? this.currentInputValue : this.defaultValue ? CommonUtils.parseInt(JSON.parse(this.defaultValue).value) :this.unlimitedValue);
  }
  setDefaultValues() {
    this.hideDefaultOptions = false;
    this.defaultOptions.type = '';
    this.handleValuesDisplay();
    //Jira #9444 - Wait for Angular to change the type before setting the new value of new type
    Promise.resolve().then(() => {
      this.handleValuesDisplay();
    });
  }

  unSetDefaultValues() {
    this.hideDefaultOptions = true;
    this.defaultOptions.type = 'number';
    //Jira #9444 - Wait for Angular to change the type before setting the new value of new type
    Promise.resolve().then(() => {
      this.handleValuesDisplay();
    });
  }

  onDefaultValueChange(val) {
    if (this.hideDefaultOptions && val.newValue === this.unlimitedValue) {
      this.setDefaultValues();
    } else if (typeof val.newValue === 'number') {
      this.unSetDefaultValues();
    }
    this.onPsChange.emit(val.newValue);
  }
}
